export default {
    'url': '/terminalTrend/map/province',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {
            'province|2-5': [
                {
                    'name|+1': ['二十九支队', '黔江区支队','二十七支队','万州区支队', '二十一支队'],
                    'value|30-100': 70,
                    'detail|4': [
                        {
                            'name|+1': ['违规外联','违规U盘','中毒情况','查杀情况'],
                            'value|100-9999': 80
                        }
                    ]
                }    
            ]
        }
    }
}


