import './index.css'
import $ from 'jquery'
import hbs from './index.hbs'

export default class Modal {
  // constructor({headers = [], onPrev, onNext}) {
  //   this.headers = headers
  //   this.onPrev = onPrev
  //   this.onNext = onNext

  //   this.dataSource = null
  // }

  /**
   * 绑定弹窗关闭事件
   * @return {void} void
   */
  bindCloseBtnClick = () => {
    $('.group-list').on('click', 'li', e => {
      $(e.currentTarget).siblings().removeClass('active')
      $(e.currentTarget).siblings().find('.details').slideUp()
      $(e.currentTarget).addClass('active')
      $(e.currentTarget).find('.details').slideDown()
    })
    $('.details').on('click', e => {
      $(e.currentTarget).parents('li').removeClass('active')
      $(e.currentTarget).slideUp()
      e.stopPropagation()
    })
    $('#btnModalClose').on('click', function() {
      $(this).off('click')
      $('#modalContainer').remove()
    })
  }

  /**
   * 渲染表格
   * @param  {Array} dataSource 数据集
   * @return {void}  void
   */
  // renderTable = dataSource => {
  //   $('#modalTable')
  //     .empty()
  //     // .append(tableHbs({
  //     //   headers: this.headers,
  //     //   dataSource
  //     // }))
  // }

  /**
   * 渲染
   * @param  {Array} dataSource 数据集
   * @return {void}  void
   */
  init = dataSource => {
    // 渲染基础组件, 并绑定事件
    $('main').prepend(hbs(dataSource))
    this.bindCloseBtnClick()
  }
}



