 import $ from 'jquery'
 import './index.css'
 import hbs from './index.hbs'

 // 引入组件
import DrawMap from '../../charts/drawMap/drawMap'
import MapData from '../../charts/drawMap/mapData'

export default class Province {
    render(data, city) {
        $('.map-main').empty().append(hbs)
        this.maxMapShow(data, city)
    }
    maxMapShow(data, city) {
        let opt = {
            width: 2000,
            height: 1600,
            scaleNum: 48,
            site: city,
            mapSty: {
              fill: 'rgba(60,109,229,0.8)',
              stroke: '#3cf9ff',
              strokeWidth: 2
            }
          } 
        let optData = {
            fatherId: '.max-map'
        }
        this.maxMap = new MapData(new DrawMap('.max-map .map-chongqing', opt), optData) 
        this.changeData(data)
    }

    changeData(data) {
        this.maxMap.render(data)
    }
} 



