export default {
    'url': '/terminalTrend/map/mainCity',
    'mock': {
        'code': 1,
        'msg': 'success',
        'result': {
            'mainCity|1-2': [
                {
                    'name|+1': ['二十二支队', '二十五支队'],
                    'value|30-100': 70,
                    'detail|4': [
                        {
                            'name|+1': ['违规外联','违规U盘','中毒情况','查杀情况'],
                            'value|100-9999': 80
                        }
                    ]
                }    
            ]
        }
    }
  }


