import $ from 'jquery'
 import './index.css'
 import hbs from './index.hbs'
 import PopWindow from '../popWindow'

const popWindow = new PopWindow()
 export default class Group {
    render(data) {
        $('.group-main').append(hbs(data[0]))
        if(data[0].value >= 90) {
            $('.group-main .label b').css('background', '#72fffd')
        } else if(data[0].value < 90 && data[0].value >= 70) {
            $('.group-main .label b').css('background', '#fff800')
        } else {
            $('.group-main .label b').css('background', '#ff5317')
        }
        $('.group-main').on('click', () => {
            popWindow.init(data)
        })
       
    }
 }


