import * as d3 from 'd3'
import _ from 'lodash'
// import commonUnit from './commonUnit'

export default class splitPie {
  /**
   * 饼图默认配置项
   */
  defaultSetting() {
    return {
      width: 190,
      height: 190,
      padding: {
        top: 0,
        right: 0,
        bottom: 0,
        left: 0
      },
      min: 0,
      max: 40, // 限制平分最多个数
      scale: 0.6, // 用于控制平分后圆的大小
      outerRadius: 60,
      innerRadius: 90,
      color: ['#1a2c3c','url(#grad1)' ],
      stroke: '#051046',
      strokeWidth: 3
    }
  }

  /**
   *  默认数据源
   */
  defaultDataSource() {
    return [33,67]
  }

  /**
   * 创建SVG
   * @param {*} id 
   * @param {*} config 
   */
  addSvg(id, config) {
    var svg = null
    var width = config.width
    var height = config.height
    var padding = config.padding
    if(d3.select(id).selectAll('svg')[0].length > 0) {
      svg = d3.select(id).selectAll('svg')
    }else {
      svg = d3.select(id)
        .append('svg')
        .attr('width', width)
        .attr('height', height)
        .style('padding', function() {
          var top = padding.top
          var bottom = padding.bottom
          var left = padding.left
          var right = padding.right
          return top + 'px ' + right + 'px ' + bottom + 'px ' + left + 'px'
        })
    }
    return svg 
  }
  /**
     *  绘制图表
     *  @param    {string} id   容器id
     *  @param    {array}  data 图表数据
     *  @param    {object} opt  图表配置项
     *  
     *  example:
     *  [
     *    {
     *      name: "江北区",
     *      value: 234
     *    }
     *  ] 
     */
    drawCharts(id, data1, opt) {
      var config = _.merge({}, this.defaultSetting(), opt)
      var data = data1 || this.defaultDataSource()
      var svg = this.addSvg(id, config)
      var nums = []
      var dataset = data
      
      var w = config.width
      var h = config.height
      var max = config.max
      var min = config.min
      var scale = config.scale

      var pie = d3.layout.pie().sort(null) // 饼图布局
      var pieData = []
      // var nums = []
      
      // 计算一个比例
      var unit = Math.ceil(d3.max(dataset) * scale / (max - min))

      for(var i = 0, len = dataset.length; i < len; i++) {
        // 根据比例得到每个值平分多少份
        var num = Math.ceil(dataset[i] / unit)
        for(var j = 1; j < num + 1; j++) {
          pieData.push(1)
        }
        // 保存平分多少份的值用于后面填充颜色
        nums.push(num)
      }

      // 获取update部分 
      var update = svg.selectAll('g')
          .data(pie(pieData))
      
      // 获取enter部分
      var enter = update.enter()

      // 获取exit部分
      var exit = update.exit()

      // 处理enter部分
      var appendG = enter.append('g')
        .attr({
          class: 'group',
          transform: 'translate(' + w / 2 + ', ' + h / 2 + ')'
        })
      var path = appendG.append('path')  
      this.addElement(path, nums, config)  

      // 处理update部分  
      var selectG = update.attr({
        class: 'group',
        transform: 'translate(' + w / 2 + ', ' + h / 2 + ')'
      })  
      path = selectG.select('path')
      this.addElement(path, nums, config)

      // 处理exit部分
      exit.remove()
    }
    
    /**
     *  @describe [添加元素]
     */
    addElement(dom, nums, config) {
      var c = 0
      var cc = nums[0]
      // 外半径
      var outerRadius = config.outerRadius 
      // 内半径
      var innerRadius = config.innerRadius
      var arc = d3.svg.arc()
          .innerRadius(innerRadius)
          .outerRadius(outerRadius)
      // 填充颜色    
      var color = config.color    
      dom.attr({
        fill: function(d, i) {
          if(i === cc) {
            c++
            cc += nums[c]
          }
          return color[c]
        },
        d: arc,
        stroke: config.stroke,
        'stroke-width': config.strokeWidth
      })
    }
}



