import $ from 'jquery'
import './index.css'
import hbs from './index.hbs'

 // 引入组件
 import DrawMap from '../../charts/drawMap/drawMap'
 import MapData from '../../charts/drawMap/mapData'

export default class mainCity {
    render(data, city) {
        $('.little-map').empty().append(hbs())
        this.maxMapShow(data, city)
    }

    /**
     * 初始化地图 
     */
    maxMapShow(data, city) {
        let opt = {
            width: 639,
            height: 582,
            scaleNum: 48,
            site: city,
            mapSty: {
              fill: 'rgba(60,109,229,0.8)',
              stroke: '#3cf9ff',
              strokeWidth: 2
            }
          }
        let optData = {
            fatherId: '.little-map',
            floatStyle: {
                space: 13,
                rectValSty: {
                    width: 38,
                    height: 26,
                    rx: 4,
                    ry: 4,
                    textSty: {
                        fontSize: 18,
                        x: 18,
                        y: 14
                    }
                },
                rectNameSty: {
                    state: false
                }
            },
            mouseFloat: {
                state: false
            }
            
        }
        this.minMap = new MapData(new DrawMap('.little-map .map-chongqing', opt), optData) 
        this.changeData(data)
    }
    
    changeData(data) {
        this.minMap.render(data)
    }
}


