import $ from 'jquery'
import './index.css'
import loader from '@/loader/loader'
import apis from './mock/index'
import { fetchInterval, fetch } from '@/util/request'
import LoadNav from '@/components/loadNav/index'

let loadNav = new LoadNav()
import {
    Header,
    MainCity,
    Group,
    Province
  } from './components'

// 组件
const header = new Header()
const mainCity = new MainCity()
const province = new Province()
const group = new Group()

let maxCity = 'chongqing'
let minCity = '222222'

loader.load({
    apis,
    init() {
        loadNav.init() //  页面导航
        $('.side-l-nav li:eq(4)').addClass('active')
        this.componentRender(maxCity, minCity)
    },

    /**
     * 渲染各个组件
     */
    componentRender(maxCity, minCity) {
        fetch('fetchLoginUsername', data => {
            $('.max-map svg, .little-map svg').remove()
            if(data.name != undefined) {
                header.render(data)
                fetchInterval('fetchMainCity', data => {
                    mainCity.render(data.mainCity, minCity)
                    fetch('fetchProvince', allData => {
                        province.render(allData.province, maxCity)
                        this.addEvent(data.mainCity, allData.province)
                    })
                })
                fetchInterval('fetchGroup', data => {
                    group.render(data.group)
                })
            } else {
                alert('请先登录')
                fetch('fetchLogOut', data => {
                    $(window).attr('location', data.url)
                })
            }
        })
        
    },

    /**
     * 添加事件
     */
    addEvent(mainData, allData){
        const _this = this
        maxCity = 'chongqing'
        minCity = '222222'
        $('body').off().on('click', '.but-swop', () => {
            let provinceData = allData
            let mainCityData = mainData
            if(maxCity === 'chongqing') {
                maxCity = '222222'
                minCity = 'chongqing'
                provinceData = mainData
                mainCityData = allData
            } else {
                console.log(maxCity)
                maxCity = 'chongqing'
                minCity = '222222'
                provinceData = allData
                mainCityData = mainData
            }
            $('.max-map svg, .little-map svg').remove()
            province.maxMapShow(provinceData, maxCity)
            mainCity.maxMapShow(mainCityData, minCity)
            
        })

        // 点击退出
        $('.user-box').on('click', '.log-out', () => {
            fetch('fetchLogOut', data => {
                $(window).attr('location', data.url)
            })
        })
    }
  }, 3360, 1890)



